import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import { Link } from 'react-router-dom';
import Icon from '../../../components/Icon';
import style from './PaymentMethod.module.css';
import MiniBox from '../../../layout/MiniBox';
import { useModuleConfig, useModulePageData } from '../../../hooks/module';
import { sendMessage } from '../../../utils/sdk';
import { OptionsData } from '../../../modules/Payments/Options';
import { Data } from '../../../modules/Payments';
import { COMMON } from '../../../const/messages';

const PaymentMethod = () => {
  const { t } = useTranslation();

  const config = useModuleConfig('payments');

  const { showBack } = useModulePageData<OptionsData, Data<'options'>['startScreen']>('options');

  return (
    <Box className="page" p="15px">
      <MiniBox
        showBack={showBack}
        onBack={() => {
          sendMessage(COMMON.BACK_PRESSED, {
            from: 'paymentType'
          });
        }}
        heading={t('payment_methods.paymentMethods')}
      >
        <Box width="100%" paddingTop="15px">
          <Box pb="20px">
            {config.showIntrabank
              ? (
                <Link to="/payments/intrabank">
                  <HStack className={style.action}>
                    <Box>
                      <Text variant="regular">{t('payment_methods.intrabank')}</Text>
                    </Box>
                    <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Link>
              ) : null }
            {config.showCheckDeposit
              ? (
                <Link to="/payments/check">
                  <HStack className={style.action}>
                    <Box>
                      <Text variant="regular">{t('payment_methods.check')}</Text>
                    </Box>
                    <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Link>
              ) : null }
            {config.showACH
              ? (
                <Link to="/payments/ach">
                  <HStack className={style.action}>
                    <Box>
                      <Text variant="regular">{t('payment_methods.ach')}</Text>
                    </Box>
                    <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Link>
              ) : null }
            {config.showDomesticWire
              ? (
                <Link to="/payments/domesticWire/address">
                  <HStack className={style.action}>
                    <Box>
                      <Text variant="regular">{t('payment_methods.domesticWire')}</Text>
                    </Box>
                    <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Link>
              ) : null }
            {config.showInternationalWire
              ? (
                <Link to="/payments/internationalWire/address">
                  <HStack className={style.action}>
                    <Box>
                      <Text variant="regular">{t('payment_methods.internationalWire')}</Text>
                    </Box>
                    <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Link>
              ) : null }
            {config.showSendaCard
              ? (
                <Link to="/payments/sendCard">
                  <HStack className={style.action}>
                    <Box>
                      <Text variant="regular">{t('payment_methods.sendVisaCard')}</Text>
                    </Box>
                    <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </HStack>
                </Link>
              ) : null }
          </Box>

        </Box>
      </MiniBox>
    </Box>
  );
};

export default PaymentMethod;
